.encart {
  position: relative;
  padding: 6vw 0;
  background: black;
  color: white;
  
  h2 {
    position: relative;
    font-size: 4vw;
    line-height: 1;
    z-index: 1;
    font-family: $font;
    max-width: 36vw;
    margin: 0;
    margin-left: 15vw;
    font-weight: 300;

    span.color--red {
      text-transform: uppercase;
    }
  }

  &_bkg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 0;
  }
}